	methods: {
		addCourse: function(course) {
			var _this = this;
			this.$http({
				method: 'post',
				url: 'http://47.102.201.109:8080/api/user/insertUser',
				data: {
					mobile: _this.user.mobile,
					password: _this.user.password,
					nickname:_this.user.nickname,
					avatar: _this.user.avatar,
					status:1
				}
			}).then(function() {
				alert('新增用户成功');
				_this.$router.push('/');
			});
		}
